p {
  margin: 0;
}

ul {
  padding: 0;
}
body{
  background-color: #dee2e6
}

.hidden {
  display: none;
}

.form {
  background: #dc3545 url('../images/bg.jpeg') no-repeat 50% -580px;
  display: flex;
  padding: 25px 0;
  display: flex;
  align-items: center;
  justify-content: center;

  .row {
    flex-grow: 1;
  }
}

.input-group {
  max-width: 500px;
}

input {
  &, &::placeholder {
    color: #dc3545 !important;
  }
}

.material-icons {
  color: #dc3545;
  display: block;
}

.stats {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px;
  justify-content: space-evenly;
  align-items: baseline;
  display: flex;
  background-color: rgba(#fff, 0.5);
}
.stats-item {
  text-align: center;
  font-size: x-small;
}

.flex-images {
  overflow: hidden;
}
.flex-images .item {
  float: left;
  margin: 4px;
  box-sizing: content-box;
  overflow: hidden;
  position: relative;
}
.flex-images .item img {
  display: block;
  width: auto;
  height: 100%;
}
.btn {
  width: 200px;
  height: 55px;
  border-radius: 3.25rem;
}
